.products{
    padding: 30px 50px;
    width: 100%;
    display: flex;
    .left{
        flex: 1;
        position: sticky;
        height: 100%;
        top: 50px;
        .filteritem{
            margin-bottom: 30px;
            .h2{
                font-weight: 400;
                margin-bottom: 20px;
            }
            .inputItem{
                margin-bottom: 10px;
                label{
                    margin-left: 10px;
                }
            }
        }
    }
    .right{
        flex: 3;
        .catImg{
            width: 100%;
            height: 300px;
            object-fit: cover;
            margin-bottom: 50px;
        }
    }
}